"use strict";

const io = require('socket.io-client');
const jwt = require('jsonwebtoken');


class RaymonClient {
    constructor() {
        this.EventHandlers = {
            _: "",
            OnConnect: "OnConnect",
            OnDisconnect: "OnDisconnect",
            OnLogin: "OnLogin",
        };

        this.socket = null;
        this._serverHandlersList = {};
    }

    handleEvent(event = this.EventHandlers._, handler) {
        this._serverHandlersList[event] = handler;
    }

    connect(ip, port) {
        this.socket = io('http://' + ip + ':' + port);
        this.socket.on('connect', () => {
            console.log("Connected.");
            if (this._serverHandlersList[this.EventHandlers.OnConnect])
                this._serverHandlersList[this.EventHandlers.OnConnect]();
        });

        this.socket.on('disconnect', (reason) => {
            console.log("Disconnect: " + reason);
            if (this._serverHandlersList[this.EventHandlers.OnDisconnect])
                this._serverHandlersList[this.EventHandlers.OnDisconnect]();
        });
    }

    loginWithToken(token) {
        this.socket
            .emit('authenticate', {token: token}) //send the jwt
            .on('authenticated', () => {
                console.log(`Authenticated. Id:  ${this.socket.id}`);
                if (this._serverHandlersList[this.EventHandlers.OnLogin])
                    this._serverHandlersList[this.EventHandlers.OnLogin](token);
            })
            .on('unauthorized', (msg) => {
                console.log(`Login failed. Message: ${JSON.stringify(msg.data)}`);
                this.socket.disconnect();
                if (this._serverHandlersList[this.EventHandlers.OnLogin])
                    this._serverHandlersList[this.EventHandlers.OnLogin](null);
            })
    }

    login(username, password) {
        const token = jwt.sign({username, password}, password);
        this.loginWithToken(token)
    }

    say(cmd, params) {
        this.socket.emit(cmd, params);
    }

    listen(cmd, func) {
        this.socket.on(cmd, (data) => {
            if (data)
                data = JSON.parse(data);
            if (func)
                func(data);
        });
    }

    disconnect() {
        this.socket.disconnect();
    }
}

module.exports = RaymonClient;